import React from "react";
import "../../styles/main/groupbutton.css";
import animation from "../../utils/animation";

const GameDevButton = (props) => {

    function getDisplay(e) {
        props.getDisplay(e.target.value);
    }

    return <button value={3}
        onClick={(e) => {
        animation.scrollDownSmooth();
        getDisplay(e);
    }} title="button" type="button" className={"btn btn-danger btn-nav-m-l"}>Game Development
    </button>
}
export default GameDevButton;