import React from "react";
import "../../styles/main/groupbutton.css";
import animation from "../../utils/animation";

const WebDevButton = (props) => {

    function getDisplay(e) {
        props.getDisplay(e.target.value);
    }

    return <button value={2}
        onClick={(e) => {
        animation.scrollDownSmooth();
        getDisplay(e);
    }} title="button" type="button" className={"btn btn-primary btn-nav-m-l"}>Web Development
    </button>
}
export default WebDevButton;