import React from "react";
import "../../styles/main/groupbutton.css";
import animation from "../../utils/animation";

const AboutMeButton = (props) => {

    function getDisplay(e) {
        props.getDisplay(e.target.value);
    }

    return <button value={1}
        onClick={(e) => {
        animation.scrollDownSmooth();
        getDisplay(e);
    }} title="button" type="button" className={"btn btn-success btn-nav-m-l"}>About Me
    </button>
}
export default AboutMeButton;