import React from "react";
import "../../styles/main/groupbutton.css";
import AboutMeButton from "./AboutMeButton"
import GameDevButton from "./GameDevButton"
import WebDevButton from "./WebDevButton"

const GroupButton = (props) => {

    function getDisplay(e) {
        console.log(e);
        props.getDisplay(e);
    }

    return <div className={"text-center mt-4"} role="group" aria-label="Basic example">
        <button style={{border: "none", background: "none"}}><AboutMeButton getDisplay={props.getDisplay} onClick={(e) => getDisplay(e)}/></button>
        <button style={{border: "none", background: "none"}}><WebDevButton getDisplay={props.getDisplay} onClick={(e) => getDisplay(e)}/></button>
        <button style={{border: "none", background: "none"}}><GameDevButton getDisplay={props.getDisplay} onClick={(e) => getDisplay(e)}/></button>

        {/* <button type="button" class="btn btnYellow">Web Pages</button> */}
    </div>
}
export default GroupButton;